<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Driver List
      </h2>      </div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Driver List</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->

<div class="col-md-12">
<div class="card">
              <div class="card-header">
                <div class="row">
                  <div class="col-sm-6"><h4 class="text-left">Search : <input type="text" name="" v-model="search" placeholder="Type Here..."></h4></div>
              </div>
              </div>
              <!-- /.card-header -->
              <div class="card-body p-0">
                <table class="table table-condensed">
                  <tr>
                    <th style="width: 12%">Driver Type</th>
                    <th style="width: 12%">Driver ID</th>
                    <th style="width: 18%">Driver Name</th>
                    <th style="width: 20%">Driver Address</th>
                    <th style="width: 12%">Driver Mobile</th>
                    <th style="width: 15%">VehicleNo</th>
                    <th style="width: 15%">Status</th>
                  </tr>
            <tbody>
                <tr v-for="(item,index) in filteredCustomers">
                    <td>{{ item.drivertype }}</td>
                    <td>{{ item.driver_id }}</td>
                    <td>{{ item.driver_name }}</td>
                    <td>{{ item.driver_address }}</td>
                    <td>{{ item.driver_phone }}</td>
                    <td>{{ item.vehicle_number }}</td>
                    <td>{{ item.status }}</td>                    
                </tr>
            </tbody>

                </table>
              </div>
              <!-- /.card-body -->
            </div>
</div>
</div>
</div>

</template>

<script>

export default {
  name: 'DriverListvw',
  data () {
    return {items: [],
      search: '',
      listDriver: [],
      data: {},
      drivermobno: '',
      headers: [
        {
          text: 'Dessert (100g serving)',
          align: 'start',
          sortable: false,
          value: 'name'
        },
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Iron (%)', value: 'iron' }
      ],
      desserts: [
        {
          name: 'Frozen Yogurt',
          calories: 159,
          fat: 6.0,
          carbs: 24,
          protein: 4.0,
          iron: '1%'
        },
        {
          name: 'Ice cream sandwich',
          calories: 237,
          fat: 9.0,
          carbs: 37,
          protein: 4.3,
          iron: '1%'
        }]}
  },
  computed:
  {
    filteredCustomers: function () {
      var self = this
      return this.items.filter(function (cust) { return cust.drivertype.indexOf(self.search) >= 0 || cust.driver_id.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.driver_name.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.driver_address.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.driver_phone.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.vehicle_number.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.status.indexOf(self.search) >= 0 })
       // return this.customers;
    }
  },
  created: function () {
    this.FetchItem()
  },
  methods: { FetchItem () {
    // console.log();
    let uri1 = this.$hostName + '/api/driver/viewDriver'
    this.axios.get(uri1).then((response) => {
      this.items = response.data
    })
  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



